@import '../../styles/breakpoints';

.sb-navbar-item {
  cursor: pointer;
  display: flex;
  height: var(--sbt-navbar-height);
  max-height: var(--sbt-navbar-height);
  overflow: hidden;

  img {
    max-height: 2rem;
  }
}

.sb-navbar-item__visibility-desktop {
  @include window-width-max-small {
    display: none;
  }
}

.sb-navbar-item__visibility-mobile {
  @include window-width-min-small {
    display: none;
  }
}
